.squeeze{max-width:1024px;margin:0 auto;padding:50px 0}.squeeze__title{font-family:"Circe",Arial,Helvetica,sans-serif;font-style:normal;font-weight:400;font-size:18px;margin-bottom:10px;color:#2a3e4a}.squeeze__form{display:flex;flex-wrap:wrap;gap:10px 20px;margin-bottom:40px}.squeeze__error{font-family:"Circe",Arial,Helvetica,sans-serif;font-style:normal;font-weight:400;font-size:18px;width:100%;color:#d11158}.squeeze__input{width:550px}.squeeze__copy-link{font-family:"Circe",Arial,Helvetica,sans-serif;font-style:normal;font-weight:400;font-size:18px;position:relative;color:#2388c5}.squeeze__copy-link:hover{border-bottom:1px solid #2388c5}.squeeze__copy-link.copied:after{font-family:"Circe",Arial,Helvetica,sans-serif;font-style:normal;font-weight:400;font-size:18px;position:absolute;top:-23px;right:-148px;display:block;width:190px;content:"Ссылка скопирована";padding:5px;background-color:#fff;border:1px solid rgba(35,136,197,.5);border-radius:7px;opacity:0;animation:3s show-hide ease}@keyframes show-hide{0%{opacity:0}30%{opacity:1}70%{opacity:1}100%{opacity:0}}